export const getStartTouch = (touchList: TouchList, startTouch: number | undefined) => {
  if (startTouch === undefined) {
    return null;
  }

  const { length } = touchList;
  for (let i = 0; i < length; i++) {
    if (touchList[i].identifier === startTouch) {
      return touchList[i];
    }
  }
  return null;
};

export const blockEvent = (e: TouchEvent) => {
  e.stopPropagation();
  e.preventDefault();
}